html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,*:before,*:after{
    box-sizing: inherit;
    margin: 0;
    scroll-behavior: smooth;
}
body{
    z-index: 1;

    font-family: $fuenteBase;
    background-color: $fondo;
    color: $blanco;
    
}
main{
    height: 100vh;
    display: grid;
    
}
.wrapper {
    display: block;
    width: 33rem;
    // margin: 4rem auto;
    margin: auto auto;
}

// fuentes
.h1-t1{
    font: bold 2rem / 2.5rem $fuenteBase;
    color: $negro;
}
.p-t1{
    font: 1.2rem / 1.6rem $fuenteBase;
}
label{
    font: bold 1.4rem / 1.9rem $fuenteBase;
}
input{
    font:  1.4rem / 1.9rem $fuenteBase;
}
.p-t1{
    color: $gris5;
}

// generales

.t-centrado{
    text-align: center;
}
.bg-on{
    background-color: $color1;
    
    &:hover{
        cursor: pointer;
    }
}
.bg-off{
    background-color: $color2;
}
.bg-red{
    background-color: $color2 !important;
}