@mixin phone {
    @media (min-width: $telefono){
        @content;
    }
}
@mixin mid {
    @media (min-width: $mid){
        @content;
    }
}
@mixin tablet {
    @media (min-width: $tablet){
        @content;
    }
}
@mixin desktop {
    @media (min-width: $desktop){
        @content;
    }
}