@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  z-index: 1;
  font-family: "Roboto", sans-serif;
  background-color: #10003f;
  color: white;
}

main {
  height: 100vh;
  display: grid;
}

.wrapper {
  display: block;
  width: 33rem;
  margin: auto auto;
}

.h1-t1 {
  font: bold 2rem/2.5rem "Roboto", sans-serif;
  color: black;
}

.p-t1 {
  font: 1.2rem/1.6rem "Roboto", sans-serif;
}

label {
  font: bold 1.4rem/1.9rem "Roboto", sans-serif;
}

input {
  font: 1.4rem/1.9rem "Roboto", sans-serif;
}

.p-t1 {
  color: #3E3E3E;
}

.t-centrado {
  text-align: center;
}

.bg-on {
  background-color: #f8077c;
}
.bg-on:hover {
  cursor: pointer;
}

.bg-off {
  background-color: #f5a8cd;
}

.bg-red {
  background-color: #f5a8cd !important;
}

.form-cont {
  background-color: white;
  border-radius: 1.5rem;
  padding: 3rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.form-cont img {
  border-radius: 50%;
  display: block;
  margin: 0 auto;
}

.formulario {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  padding-top: 1rem;
}
.formulario .campo {
  display: flex;
  flex-direction: column;
  overflow: auto;
  color: #3E3E3E;
}
.formulario .campo-cont {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.campo label {
  margin: 0 1rem;
}
.campo input {
  padding: 1rem;
  border-radius: 1rem;
}
.campo .campo--i {
  border: solid 1px #E0E0E0;
  filter: drop-shadow(0 0 0.5rem #E0E0E0);
  margin: 1rem;
}
.campo .campo--i:focus {
  outline: none;
}
.campo .campo--i:valid {
  background-color: #e8f0fe;
}
.campo .campo--button {
  color: white;
  margin: 0 1rem;
  border: none;
  font-weight: 500;
  transition: 1s ease;
}

.c_numero--i {
  background: url("../img/credit-card.webp");
  background-size: 2rem;
  background-repeat: no-repeat;
  background-position: 96% center;
}

.c_codigo--i {
  background: url("../img/cvv.webp");
  background-size: 1.7rem;
  background-repeat: no-repeat;
  background-position: 91% center;
}

.c_fecha--l {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 14rem;
}
@media (min-width: 768px) {
  .c_fecha--l {
    text-overflow: unset;
    white-space: unset;
    overflow: auto;
    width: auto;
  }
}

.c_fecha--i, .c_fecha--l {
  margin-right: 0 !important;
}

.c_codigo--i, .c_codigo--l {
  margin-left: 0 !important;
}