.form-cont{
    background-color: $blanco;
    border-radius: 1.5rem;
    padding: 3rem 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;

    img{
        border-radius: 50%;
        display: block;
        margin: 0 auto;
    }
}

.formulario{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    padding-top: 1rem;

    .campo{
        display: flex;
        flex-direction: column;
        // gap: 1rem;
        overflow: auto;
        color: $gris5;
    }

    .campo-cont{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    }
}

.campo{

    label{
        margin:0 1rem;
    }
    
    input{
        padding: 1rem;
        border-radius: 1rem;
    }

    .campo--i{
        border: solid 1px $gris1;
        filter: drop-shadow(0 0 0.5rem $gris1);
        margin: 1rem;
        
        &:focus{
            outline: none;
        }
        &:valid{
            background-color: $color3;
            // filter: drop-shadow(0 0 0.1rem $gris1);
        }
    }
    
    .campo--button{
        color: $blanco;
        margin: 0 1rem;
        border: none;
        font-weight: 500;
        transition: 1s ease;
    }
}
.c_numero--i{
    background: url('../img/credit-card.webp');
    background-size: 2rem;
    background-repeat: no-repeat;
    background-position: 96% center;
    
}
.c_codigo--i{
    background: url('../img/cvv.webp');
    background-size: 1.7rem;
    background-repeat: no-repeat;
    background-position: 91% center;
}
.c_fecha--l{
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width:14rem; // some width

    @include tablet {
        text-overflow: unset;
        white-space: unset;
        overflow: auto;
        width:auto; // some width
    }
}
.c_fecha--i, .c_fecha--l{
    margin-right: 0 !important;
}
.c_codigo--i, .c_codigo--l{
    margin-left: 0 !important;
}